import React, { useEffect } from 'react'
import gsap from 'gsap'

const ParallaxItem = ({ image, head, parallaxTrigger, startAction, elementTrigger }) => {

    const classes = ['parallax-item']

    if(elementTrigger) {
        classes.push(elementTrigger) 
    }

    useEffect(() => {

        gsap.to(`.${elementTrigger}`, {
            yPercent: -50,
            scrollTrigger: {
                start: `${startAction}`,
                trigger: `${parallaxTrigger}`,
                scrub: true,
            }
        });

    }, [parallaxTrigger, startAction, elementTrigger])


    return (
        <div className="parallax-container">
            <div className={classes.join(' ')}>
                {head ? <h2>{head}</h2> : ''}
                <img src={image} alt="Swim System" />
            </div>
        </div>
    )
}

export default ParallaxItem